"use client";

import { motion } from "framer-motion";
import { PlayCircle, Plus, Info, Check } from "lucide-react";
import Image from "next/image";
import Link from "next/link";
import { useState, useContext } from "react";
import clsx from "clsx";
import { WatchlistContext } from "@/context/watchlist-context";

interface Movie {
    id: number;
    title?: string;
    name?: string; // For TV
    poster_path: string;
    vote_average: number;
    release_date?: string;
    first_air_date?: string; // For TV
    overview?: string;
}

interface MovieCardProps {
    movie: Movie;
    className?: string;
}

export function MovieCard({ movie, className }: MovieCardProps) {
    const [isHovered, setIsHovered] = useState(false);

    // Watchlist Logic
    const { has, add, remove } = useContext(WatchlistContext) as any;
    // Determine type based on props (crude but effective for combined card)
    const type = movie.name ? 'tv' : 'movie';
    const isWatchlisted = has(movie.id, type);

    const handleWatchlist = (e: React.MouseEvent) => {
        e.preventDefault();
        e.stopPropagation();
        if (isWatchlisted) {
            remove(movie.id, type);
        } else {
            add({ ...movie, type });
        }
    };

    const title = movie.title || movie.name;
    const date = movie.release_date || movie.first_air_date;
    const year = date ? new Date(date).getFullYear() : "N/A";

    return (
        <motion.div
            className={clsx("relative group rounded-xl overflow-hidden cursor-pointer", className)}
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            onMouseEnter={() => setIsHovered(true)}
            onMouseLeave={() => setIsHovered(false)}
        >
            {/* Poster */}
            <div className="relative aspect-[2/3] w-full bg-bg-card">
                {movie.poster_path ? (
                    <Image
                        src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                        alt={title || "Movie"}
                        fill
                        className={clsx(
                            "object-cover transition-transform duration-500",
                            isHovered ? "scale-105 saturate-100" : "scale-100 saturate-[0.8]"
                        )}
                        sizes="(max-width: 768px) 50vw, 33vw"
                    />
                ) : (
                    <div className="w-full h-full flex items-center justify-center text-text-muted">No Image</div>
                )}

                {/* Cinematic Gradient Overlay */}
                <div className="absolute inset-0 bg-gradient-to-t from-black via-black/40 to-transparent opacity-80" />

                {/* Hover Actions Overlay */}
                <div
                    className={clsx(
                        "absolute inset-0 flex flex-col items-center justify-center gap-4 bg-black/60 backdrop-blur-sm transition-opacity duration-300",
                        isHovered ? "opacity-100" : "opacity-0"
                    )}
                >
                    <Link href={`/${type === 'tv' ? 'movie' : 'movie'}/${movie.id}`}> {/* Using movie route for now as TV route isn't fully separate detail page yet or we reuse movie detail */}
                        <div className="flex gap-4">
                            <button className="text-white hover:text-accent-primary transition-colors">
                                <PlayCircle size={40} className="drop-shadow-lg" />
                            </button>
                        </div>
                    </Link>

                    <div className="flex gap-3 text-white">
                        <button
                            onClick={handleWatchlist}
                            className={clsx(
                                "flex items-center gap-2 px-3 py-1.5 rounded-full border text-xs font-medium transition-all",
                                isWatchlisted
                                    ? "bg-accent-primary border-accent-primary text-white"
                                    : "bg-white/10 hover:bg-accent-primary hover:border-transparent border-white/20"
                            )}
                        >
                            {isWatchlisted ? <Check size={14} /> : <Plus size={14} />}
                            {isWatchlisted ? "Added" : "Watchlist"}
                        </button>
                        <Link href={`/${type === 'tv' ? 'movie' : 'movie'}/${movie.id}`}>
                            <button className="flex items-center gap-2 px-3 py-1.5 rounded-full bg-white/10 hover:bg-white hover:text-black border border-white/20 text-xs font-medium transition-all">
                                <Info size={14} /> Info
                            </button>
                        </Link>
                    </div>
                </div>
            </div>

            {/* Title & Metadata */}
            <div className="absolute bottom-0 left-0 right-0 p-4 transform transition-transform duration-300 group-hover:translate-y-2">
                <h3 className="text-white font-display font-medium text-lg truncate drop-shadow-md">
                    {title}
                </h3>
                <div className="flex items-center gap-2 mt-1">
                    <span className="text-accent-primary text-xs font-bold px-1.5 py-0.5 rounded bg-accent-primary/10 border border-accent-primary/20">
                        {(movie.vote_average * 10).toFixed(0)}% Match
                    </span>
                    <span className="text-text-muted text-xs">
                        {year}
                    </span>
                </div>
            </div>
        </motion.div>
    );
}
